'use client'

import { useState, Fragment } from 'react'
import { Dialog, Transition } from '@headlessui/react'

interface ProjectFormData {
  name: string
  goal: string
  dueDate: string
  mode: 'solo' | 'collab'
  teamMembers: string[]
  repoOption: 'new' | 'existing'
  existingRepoUrl: string
}

interface AddProjectModalProps {
  isOpen: boolean
  onClose: () => void
}

export default function AddProjectModal({isOpen, onClose }: AddProjectModalProps) {
  const [formData, setFormData] = useState<ProjectFormData>({
    name: '',
    goal: '',
    dueDate: '',
    mode: 'solo',
    teamMembers: [],
    repoOption: 'new',
    existingRepoUrl: ''
  })

  const updateField = (field: keyof ProjectFormData, value: any) => {
    setFormData(prev => ({ ...prev, [field]: value }))
  }

  const handleSubmit = async () => {
    try {
      const response = await fetch('http://localhost:8000/api/projects', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(formData)
      })
      
      if (!response.ok) throw new Error('Failed')
      const data = await response.json()
      alert(`‚úÖ Created! ID: ${data.projectId}`)
      onClose()
      
    } catch (error) {
      alert('‚ùå Failed')
    }
  }

  return (
    <Transition appear show={isOpen} as={Fragment}>
      <Dialog as="div" className="relative z-50" onClose={onClose}>
        
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0"
        >
          <div className="fixed inset-0 bg-black/80" />
        </Transition.Child>

        <div className="fixed inset-0 overflow-y-auto">
          <div className="flex min-h-full items-center justify-center p-6">
            <Transition.Child
              as={Fragment}
              enter="ease-out duration-300"
              enterFrom="opacity-0 scale-95"
              enterTo="opacity-100 scale-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100 scale-100"
              leaveTo="opacity-0 scale-95"
            >
              <Dialog.Panel className="w-full max-w-lg transform overflow-hidden rounded-2xl bg-neutral-900 border border-neutral-700 shadow-2xl transition-all">
                
                {/* Header */}
                <div className="bg-neutral-800 border-b border-neutral-700 px-8 py-5 flex justify-between items-center">
                  <Dialog.Title className="text-xl font-semibold text-white">
                    New Project
                  </Dialog.Title>
                  <button onClick={onClose} className="text-neutral-400 hover:text-white text-2xl">
                    √ó
                  </button>
                </div>

                {/* Body - Lots of padding */}
                <div className="px-12 py-8">
                  
                  {/* Inner container for inputs */}
                  <div className="space-y-5">
                    
                    <div className="flex flex-col items-center">
                      <label className="block text-sm font-medium text-neutral-300 mb-2 self-start">
                        Name <span className="text-red-400">*</span>
                      </label>
                      <input
                        type="text"
                        value={formData.name}
                        onChange={(e) => updateField('name', e.target.value)}
                        placeholder="My Project"
                        className="w-full max-w-sm bg-neutral-800 text-white px-4 py-2.5 rounded-lg border border-neutral-600 text-sm placeholder:text-left focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                      />
                    </div>

                    <div className="flex flex-col items-center">
                      <label className="block text-sm font-medium text-neutral-300 mb-2 self-start">
                        Goal <span className="text-red-400">*</span>
                      </label>
                      <textarea
                        value={formData.goal}
                        onChange={(e) => updateField('goal', e.target.value)}
                        placeholder="What are you building?"
                        rows={2}
                        className="w-full max-w-sm bg-neutral-800 text-white px-4 py-2.5 rounded-lg border border-neutral-600 text-sm placeholder:text-left focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent resize-none"
                      />
                    </div>

                    <div className="flex flex-col items-center">
                      <label className="block text-sm font-medium text-neutral-300 mb-2 self-start">
                        Due Date <span className="text-red-400">*</span>
                      </label>
                      <input
                        type="date"
                        value={formData.dueDate}
                        onChange={(e) => updateField('dueDate', e.target.value)}
                        className="w-full max-w-sm bg-neutral-800 text-white px-4 py-2.5 rounded-lg border border-neutral-600 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                      />
                    </div>

                    <div className="flex flex-col items-center">
                      <label className="block text-sm font-medium text-neutral-300 mb-3 self-start">
                        Mode
                      </label>
                      <div className="flex justify-center gap-3 w-full max-w-sm">
                        <button
                          onClick={() => updateField('mode', 'solo')}
                          className={`flex-1 py-2.5 px-5 rounded-xl text-sm font-semibold border-2 transition-all ${
                            formData.mode === 'solo'
                              ? 'bg-emerald-600 text-white border-emerald-500 shadow-lg shadow-emerald-600/30'
                              : 'bg-neutral-700 text-neutral-200 border-neutral-600 hover:bg-neutral-650'
                          }`}
                        >
                          Solo
                        </button>
                        <button
                          onClick={() => updateField('mode', 'collab')}
                          className={`flex-1 py-2.5 px-5 rounded-xl text-sm font-semibold border-2 transition-all ${
                            formData.mode === 'collab'
                              ? 'bg-emerald-600 text-white border-emerald-500 shadow-lg shadow-emerald-600/30'
                              : 'bg-neutral-700 text-neutral-200 border-neutral-600 hover:bg-neutral-650'
                          }`}
                        >
                          Team
                        </button>
                      </div>
                    </div>

                    <div className="flex flex-col items-center">
                      <label className="block text-sm font-medium text-neutral-300 mb-3 self-start">
                        Repository
                      </label>
                      <div className="space-y-3 w-full max-w-sm">
                        <button
                          onClick={() => updateField('repoOption', 'new')}
                          className={`w-full py-3 px-5 rounded-xl text-sm font-semibold text-left border-2 transition-all ${
                            formData.repoOption === 'new'
                              ? 'bg-emerald-600 text-white border-emerald-500 shadow-lg shadow-emerald-600/30'
                              : 'bg-neutral-700 text-neutral-200 border-neutral-600 hover:bg-neutral-650'
                          }`}
                        >
                          üÜï Create New Repository
                        </button>
                        <button
                          onClick={() => updateField('repoOption', 'existing')}
                          className={`w-full py-3 px-5 rounded-xl text-sm font-semibold text-left border-2 transition-all ${
                            formData.repoOption === 'existing'
                              ? 'bg-emerald-600 text-white border-emerald-500 shadow-lg shadow-emerald-600/30'
                              : 'bg-neutral-700 text-neutral-200 border-neutral-600 hover:bg-neutral-650'
                          }`}
                        >
                          üîó Link Existing Repository
                        </button>
                      </div>
                    </div>

                    {formData.repoOption === 'existing' && (
                      <div className="flex flex-col items-center">
                        <label className="block text-sm font-medium text-neutral-300 mb-2 self-start">
                          Repo URL
                        </label>
                        <input
                          type="url"
                          value={formData.existingRepoUrl}
                          onChange={(e) => updateField('existingRepoUrl', e.target.value)}
                          placeholder="https://github.com/user/repo"
                          className="w-full max-w-sm bg-neutral-800 text-white px-4 py-2.5 rounded-lg border border-neutral-600 text-sm placeholder:text-left focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                        />
                      </div>
                    )}

                  </div>
                </div>

                {/* Footer */}
                <div className="bg-neutral-800 border-t border-neutral-700 px-12 py-5">
                  <button
                    onClick={handleSubmit}
                    disabled={!formData.name || !formData.goal || !formData.dueDate}
                    className="w-full max-w-sm mx-auto block bg-blue-600 hover:bg-blue-700 disabled:bg-neutral-700 disabled:text-neutral-500 disabled:cursor-not-allowed text-white py-3 rounded-xl font-semibold transition-all shadow-lg disabled:shadow-none"
                  >
                    Create Project ‚Üí
                  </button>
                </div>

              </Dialog.Panel>
            </Transition.Child>
          </div>
        </div>
      </Dialog>
    </Transition>
  )
}